import React, { useState } from 'react';
import Card from '@mui/material/Card';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import InputAdornment from '@mui/material/InputAdornment';
import IconButton from '@mui/material/IconButton';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { UserInterface } from 'src/models/users';

//icons
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

interface UserCredentials extends UserInterface {
    email: string;
    password: string;
}

interface Props {
    userData: UserCredentials,
    onTextChange: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void,
    onSelectChange: (event: SelectChangeEvent) => void,
    onSubmitForm: (event: React.FormEvent<HTMLFormElement>) => void
}

function UserForm({ userData, onTextChange, onSelectChange, onSubmitForm }: Props) {
    const [showPassword, setShowPassword] = useState<boolean>(false);

    return (
        <Card sx={{ width: '100%', padding: 3 }}>
            <form onSubmit={event => onSubmitForm(event)}>
                <Grid container spacing={2}>
                    <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                        <TextField 
                            fullWidth
                            required
                            name="firstName" 
                            label="First Name" 
                            variant="outlined"
                            value={userData.firstName}
                            onChange={event => onTextChange(event)}
                        />
                    </Grid>
                    <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                        <TextField 
                            fullWidth
                            required
                            name="lastName" 
                            label="Last Name" 
                            variant="outlined"
                            value={userData.lastName}
                            onChange={event => onTextChange(event)}
                        />
                    </Grid>
                     <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                        <Select
                            fullWidth
                            required
                            name="gender"
                            label="Gender"
                            value={userData.gender}
                            onChange={event => onSelectChange(event)}
                        >
                            <MenuItem value="Male">Male</MenuItem>
                            <MenuItem value="Female">Female</MenuItem>
                        </Select>
                    </Grid>
                    <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                        <TextField 
                            fullWidth
                            required
                            type="email"
                            name="email" 
                            label="User Email" 
                            variant="outlined"
                            value={userData.email}
                            onChange={event => onTextChange(event)}
                        />
                    </Grid>
                    <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                        <TextField 
                            fullWidth
                            required
                            inputProps={{ maxLength: 13, minLength: 9 }}
                            name="contact" 
                            label="Contact Number" 
                            variant="outlined"
                            value={userData.contact}
                            onChange={event => onTextChange(event)}
                        />
                    </Grid>
                    <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                        <TextField 
                            fullWidth
                            required
                            type={!showPassword ? "password" : "text"}
                            name="password" 
                            label="Password" 
                            variant="outlined" 
                            value={userData.password}
                            onChange={event => onTextChange(event)}
                            InputProps={{
                                endAdornment: <InputAdornment position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        edge="end"
                                        onClick={e => setShowPassword(!showPassword)}
                                        >
                                            {!showPassword ?  <VisibilityOff /> : <Visibility />}
                                        </IconButton>
                                    </InputAdornment>
                            }}
                        />
                    </Grid>
                    <Grid item sm={12} md={12} sx={{ width: '100%' }}>
                        <Button type="submit" variant="contained" fullWidth>SAVE</Button> 
                    </Grid>
                </Grid>
            </form>
        </Card>
    )
}

export default UserForm
