import React, { useState } from 'react';
import { Helmet } from 'react-helmet-async';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useNavigate } from 'react-router-dom';
import { SelectChangeEvent } from '@mui/material/Select';

//components 
import UserForm from '../create/UserForm';
import SnackbarApp, { SnackbarProps } from 'src/components/Snackbar';

//firebase
import { UserInterface, convertUserQuerySnapshots } from 'src/models/users';
import { doc, setDoc, getDoc } from "firebase/firestore";
import { useFirebaseAuth } from 'src/contexts/FirebaseAuthProvider';
import axios from 'axios';

//icons
import CloseIcon from '@mui/icons-material/Close';

interface UserCredentials extends UserInterface {
    email: string;
    password: string;
}

function EditUser() {
    const matches = useMediaQuery('(min-width:600px)');
    const navigate = useNavigate();
    const { database, currentUser } = useFirebaseAuth();
    const [snackbarApp, setSnackBarApp] = useState<SnackbarProps>({
        openSnackbar: false,
        success: true,
        message: ''
    });
    const [userForm, setUserForm] = useState<UserCredentials>({
        firstName: '',
        lastName: '',
        contact: '',
        email: '',
        password: '',
        gender: 'Male',
    });

    React.useEffect(() => {
        async function getProduct() {
            const userRef = doc(database, "accounts", currentUser.uid);
            const userSnap = await getDoc(userRef);

            if (userSnap.exists()) {
                const currUser = convertUserQuerySnapshots(userSnap);

                const userData: UserCredentials = {
                    contact: currUser.getUser().contact,
                    firstName: currUser.getUser().firstName,
                    lastName: currUser.getUser().lastName,
                    gender: currUser.getUser().gender,
                    userId: currUser.getUser().userId,
                    email: currentUser.email,
                    password: ''
                }

                setUserForm(state => userData)
            } else {
                navigate('/404');
            }
        }

        getProduct();
    }, [currentUser, database, navigate])

    function onTextChange(event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) {
        setUserForm({ ...userForm, [event.target.name]: event.target.value });
    }

    function onSelectChange(event: SelectChangeEvent) {
        setUserForm({ ...userForm, [event.target.name]: event.target.value });
    };

    function saveUser(userId: string) {
        const newUser: UserInterface = {
            firstName: userForm.firstName,
            lastName: userForm.lastName,
            contact: userForm.contact,
            gender: userForm.gender
        }

        setDoc(doc(database, "accounts", userId), newUser).then(res => {
            navigate('/dashboards/users');
        }).catch(err => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'Failed to save user.' });
        })
    }

    function onSubmitForm(event: React.FormEvent<HTMLFormElement>) {
        event.preventDefault();

        const body = JSON.stringify({
            email: userForm.email,
            name: userForm.firstName + ' ' + userForm.lastName,
            contact: userForm.contact,
            password: userForm.password
        });

        axios.post('https://butiladmin.herokuapp.com/api/jsdhfyyrhv7&ggeimcv_$hrjeuifcv/user/update/' + currentUser.uid, body, { headers: { 'Content-Type': 'application/json'}}).then(res => {
            const data = res.data;
            saveUser(data.uid);
        }).catch(error => {
            setSnackBarApp({ ...snackbarApp, openSnackbar: true, success: false, message: 'User alredy exist.' });
        })
    }

    return (
        <>
            <Helmet>
                <title>Edit Profile</title>
            </Helmet>
            <Container maxWidth="sm" sx={{ mb: 5 }}>
                <Box sx={{ 
                        display: 'flex',
                        justifyContent: 'space-between',
                        alignItems: 'center',
                        marginTop: 5,
                        marginBottom: 3
                }}>
                    <Typography variant="h1" color="primary">Edit Profile</Typography>
                    {!matches ? 
                        <IconButton onClick={event => navigate('/dashboards/users')} color="error">
                            <CloseIcon />
                        </IconButton>
                    :
                        <Button variant="contained" color="error" startIcon={<CloseIcon />} onClick={event => navigate('/dashboards/users')}>
                            Cancel
                        </Button> 
                    }
                </Box>
                
                <UserForm userData={userForm} 
                    onTextChange={event => onTextChange(event)} 
                    onSelectChange={event => onSelectChange(event)} 
                    onSubmitForm={event => onSubmitForm(event)}
                />
                
            </Container>
            <SnackbarApp openSnackbar={snackbarApp.openSnackbar} success={snackbarApp.success} message={snackbarApp.message} setOpenSnackbar={value => setSnackBarApp({ ...snackbarApp, openSnackbar: value })} />
        </>
    )
}

export default EditUser
